import React from 'react'

interface TextAreaProps {
  value: string
  onChange: (value: string) => void
  placeholder?: string
  rows?: number
  disabled?: boolean
  className?: string
  label?: string
}

export const TextArea: React.FC<TextAreaProps> = ({
  value,
  onChange,
  placeholder = '',
  rows = 4,
  disabled = false,
  className = '',
  label
}) => {
  return (
    <div className="w-full">
      {label && (
        <label className="block text-sm font-medium text-pantun-text mb-2">
          {label}
        </label>
      )}
      <textarea
        value={value}
        onChange={(e) => onChange(e.target.value)}
        placeholder={placeholder}
        rows={rows}
        disabled={disabled}
        className={`
          w-full px-4 py-3 border-2 border-kampung-brown/20 rounded-lg
          bg-white/90 backdrop-blur-sm
          focus:outline-none focus:ring-2 focus:ring-pantun-primary focus:border-pantun-primary
          disabled:opacity-50 disabled:cursor-not-allowed
          transition-all duration-200
          ${className}
        `}
      />
    </div>
  )
}
